export function createBitAttrs(bit, parts) {
    const attrs = {};
    parts.forEach((part) => {
        attrs[part] = {
            [`data-bits-${bit}-${part}`]: ""
        };
    });
    return (part) => attrs[part];
}
export function disabledAttrs(disabled) {
    return disabled ? { "aria-disabled": true, "data-disabled": "" } : {};
}
